export default function ListRow(props) {
    // const [title, useTitle] = useState('头部');
  let { item, clickRowHandle, classN } = props

  console.log('item', item)

    function clickRow() {
      clickRowHandle(item)
    }
    
    return (
      <li onClick={clickRow} className={`row ${classN}`}>
        <div className="lebal">{item.name}</div>
        {item.img && <div className="img_box"><img className="img" src={item.img} /></div>}
        {/* {item.msg && <div className="val">{item.msg}</div>} */}
      </li>
    )
}